.item {
  background-color: var(--mainwhite);
  line-height:1.5rem;
  padding:10px 20px;
  margin-bottom:1rem;
  border: 1px solid #e0e0e0;
  display:flex;
  justify-content: space-between;
  transition :all 0.3s linear;
}
.item:hover {
  width:100%;
  transform: scale(1.02);
}

.info {
  flex: 0 0 60%;
  display: flex;
  justify-content: space-between;
}

.expense {
  margin-right:2rem;
}

.amound {
  font-weight:300;
  font-size:1rem;
  padding:1px 3px;
}

.clearBtn, 
.editBtn {
  background: none;
  border:none;
  font-size:1.2rem;
  outline:none;
  cursor:pointer;
}

.clearBtn {
  color:var(--mainRed);
}
.editBtn {
  color:var(--mainGreen);
}